* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  font-family: "Montserrat", sans-serif;
}
body {
  background: lavender;
  padding: 0 16px;
}

header {
  background-color: lavender;
  margin: auto -16px;
  padding: 16px 32px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
}

header h1 {
  color: #fff;
  font-family: "McLaren", cursive;
  font-weight: 200;
}

footer {
  position: absolute;
  text-align: center;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}

footer p {
  color: #ccc;
}

intro {
  text-align: center;
  padding-top: 150px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}
container{
  background: lavender;
}
fab {
  color:black;
}


